import React, { useEffect } from 'react';
import { Canvas } from './Canvas';

export interface NodeType {
    val: string | number;
}

export const Node: React.FC<NodeType> = (props) => {
    const { val } = props;
    useEffect(() => {
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(Math.random(), Math.random(), 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText(val, Math.random() - 10, Math.random() + 10);
    }, []);

    return (
        <>
            <Canvas></Canvas>
        </>
    );
};
